@import "../../colors.scss";

.use_technology{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    .title{
        color: white;
        align-items: center;
        margin: 20px;
        h2{
            display: flex;
            justify-content: flex-start;
            margin-bottom: 25px;
            font-style: italic;
            font-weight: 200;
            font-family:"Roboto mono", sans-serif ;
            font-size: 18px;
            }
    }
    .icons{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        height: 100%;

        img{
            display: flex;
            width: 70px;
            height: 70px;
            margin: 0 10px; 
            justify-content: space-around;

               &:hover{
                scale: 1.2;
            }
        }
    }
    
}
